@import "../../partials/typography";
@import "../../partials/mixins";
@import "../../partials/breakpoints";
@import "../../partials/variables";
/************************************HERO CONTAINER****************************/

.herovideo {
 

    &__background {
        display: flex;
    position: relative;
    justify-content:center;
    background-color: $black;
    }
    &__video {
        width:100%;
        margin:0;

        @include tablet {
            width: 94%;
            margin: 0 24px;
            display: flex;
            background-color:$black;
        }

        @include desktop {
            margin: 0;
            width: 100%;
        }
    }
    


    &__box {
        margin: 4.0rem 0; //64px 
        z-index: 1;
        margin-left: 5.0vw;
        &--mobile-box-size-biography {
            min-width: 224px; //224px 
            max-width: 30.0vw; //224px
        }
        &--mobile-box-size-shows {
            min-width: 288px; //288px 
            max-width: 90.0vw; //288px 
        }
        @include tablet {
            margin: 6.0rem 0; //96px 
            margin-left: 3.1vw;
            &--tablet-box-size-biography {
                min-width: 510px; //apx 500px
                max-width: 39.8vw; //apx 500px
            }
            &--tablet-box-size-shows {
                min-width: 569px; //564px 74.1
                max-width: 74.1vw; //564px 
            }
        }
        @include desktop {
            margin: 10.0rem 0; //160px
            margin-left: 6.25vw;
            &--desktop-box-size-biography {
                width: 41%;
            }
            &--desktop-box-size-shows {
                min-width: 568px; //552px 484px
                max-width: 30.8vw; //552px  484px 37.8
            }
        }
    }
    &__title {
        margin: 0 0 24px 0;
        @include h3-subheader-white;
        @include tablet {
            @include h2-header-white;
            margin: 0 0 48px 0;
        }
    }
    &__text {
        z-index: 1;
        &--font-size-shows {
            @include h1-desktop-size-that-should-be-available-on-mobile; //based on the mockup this is the ideal size for the mobile hero heading looks perfect color: $white;
            color: $white;
            @include tablet {
                @include h1-hero-heading;
            }
            @include desktop {
                @include h1-hero-heading;
            }
        }
        &--font-size-biography {
            @include h1-main-header;
            @include tablet {
                @include h1-hero-heading;
                //564px 74.1
            }
        }
    }
    &__soundcloud {
        @include standard-padding-left-right;
        position: absolute;
        z-index: 1;
        align-self: flex-end; //push soundcloud to bottom 
        margin: 0 0 20.0vw 0; //64px
        width: 90.0vw; // 288px 
        height: 30.0vw; //96px 
        padding: 0 0 0 16px;
        @include tablet {
            margin: 0 0 6.3vw 0; //48px match 
            width: 436px;
            height: 146px;
        }
        @include desktop {
            padding: 0;
            margin: 0 0 17.7rem 0; //274px 
            position: relative;
            height: 146px;
            width: 436px;
        }
    }
}