@import "../../partials/mixins";
@import "../../partials/breakpoints";
@import "../../partials/variables";
/************************************HERO CONTAINER****************************/

/************************************PHOTO GALLERY SECTION************************************/

.subvideo {
    display: flex;
    flex-wrap: wrap;
    background-color: $alabaster;
    @include standard-padding-left-right;
 

    &__title {
        @include h5-labels;
        margin: 2rem 0 1.5rem; //margin top 40px bottom 40px all viewwidths
    }
    &__photo {
        margin: 0px 0 24rem 0px; //24px bottom same for all viewwidths
       width:37.5%; //120px
      height: 21.8%;// 70px
      object-fit: cover;
   
    }
    &__video-information{
        width: 50%;
        @include h4-list-contact-information;
    }
    &__channel{
        width: 50%;
        @include h4-list-contact-information;
    }
    &__video-container{width:50%;}
}