@import "../../partials/typography";
@import "../../partials/mixins";
@import "../../partials/breakpoints";
@import "../../partials/variables";
/************************************MAIN NAV************************************/

.header {
    @include standard-padding-left-right;
    background-color: $white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    color: $silver;
    height: 169px; //80px mobile 25.0vw
    //max-height: 25vw; //80px tablet
  
        display: flex;
        flex-direction: column;
        padding: 0 16px;
        margin-bottom: 16px;
    
        @include tablet {
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            padding: 0 24px;
            height: 80px;
            margin: 0;
        }
    
        @include desktop {
            padding: 0 180px;
            justify-content: space-between;
        }
    
        &__logo {
            height: 48px;
            width: 50%;
            align-self: center;
    
            @include tablet {
                height: 25px;
                width: 20%;
                margin-right: 150px;
            }
    
            @include desktop {
                height: 30px;
                margin-right: 345px;
            }
        }

        &__input {
            background-image: url("../../assets/Icons/Icon-search.svg");
            background-repeat: no-repeat;
            background-size: 20px 20px;
            background-position-y: center;
            background-position-x: 15px;
            padding-left: 40px;
            height: 40px;
            border-radius: 5px;
            border: solid 1px $mercury;
            background-color: $alabaster;
            width:100%;
    
            @include tablet {
                width: 170px;
            }
    
            &:active {
                border-color: $brainflix-blue;
            }
    
            &:hover {
                border-color: $brainflix-blue;
            }
    
    
            @include desktop {
                width: 260px;
                height: 45px;
                
            }
        }
        &__container {
            margin: 8px 0 0 0;
            height: 48px;
            display:flex;
    
            @include tablet {
                width: 25%;
                margin: 0;
            }
    
            @include desktop {
                width: 20%;
                justify-content: flex-end;
            }
        &__button {
            background-image: url("../../assets/Icons/Icon-upload.svg");
            background-repeat: no-repeat;
            //background-size: 15px 15px;
           // background-position-y: center;
           // background-position-x: 25%;
           @include button;
            //height: 100%;
            background-color: $brainflix-blue;
            border: 0;
            border-radius: 5px;
            color: $white;
            width: 80vw;

            &:hover {
                background-color: $brainflix-dark-blue-hover;
            }

            @include tablet {
                background-position-x: 15px;
                padding-left: 20px;
                height: 45px;
            }

            @include desktop {
                height: 49px;
                width: 60%;
            }
        }
    
        &__photo {
            width: 48px;
            border-radius: 50%;
            height: 100%;
            margin-left: 16px;
        }
    }
    
}