@import "../../partials/typography";
@import "../../partials/mixins";
@import "../../partials/breakpoints";
@import "../../partials/variables";

/************************************ABOUT SECTION************************************/

.about {
    background-color: $white;
    color: $black;
   // display:flex;
    //flex-direction: column;
    @include standard-padding-left-right;
    margin: 0 0 40px 0; //40px bottom
    @include tablet {
        margin: 0 0 64px 0;
      }
      @include tablet {
        margin: 0 0 20px 0;
      }
    &__title {
       margin: 0 0 16px 0; //16px bottom
        @include h1-main-header-black-bold;
        @include tablet {
            margin: 0 0 16px 0; //16px bottom
        }
    }

    &__content {
    
        @include tablet{
            display:flex;
            justify-content: space-between;
    }
    }
    &__info-partition {

       width:100%;
        
    }
    &__status-partition {
      
       padding: 0 0 24px 0;
        width:20%;
        
    }
    &__name {
        display:flex;
       // @include h4-paragraphs-and-comments;
        //margin: 0 0 2.5rem 0; //0 top 0 right 40px bottom 0px left
        @include h3-subheader;
        @include tablet {
        // margin: 0 0 2.5rem 0; //0 0 40 0
        }
        @include desktop {
          //  margin: 0 0 2.5rem 0; //48px top 0 px right  40px bottom 
        }
    }
    &__names {
        @include h4-paragraphs-and-comments;
        margin: 0 0 1.5rem 0; //0 0 24px
        @include tablet {
            margin: 0 0 2.5rem 0; //168 left 32 bottom
        }
        @include desktop {
            margin: 0 0 2.5rem 0; //48px top 0 px right  40px bottom
        }
    }
    &__views {
        @include h4-paragraphs-and-comments;
        margin: 0 0 1.5rem 0; //0 0 24px
        @include tablet {
            margin: 0 0 2.5rem 0; //168 left 32 bottom
        }
        @include desktop {
            margin: 0 0 2.5rem 0; //48px top 0 px right  40px bottom
        }
    }
    &__date{
    @include h5-labels;
    @include tablet {
        align-self: center;
    }
     }
    &__text {
        @include p-paragraph;
        margin: 0 0 2.5rem; //top24 bottom40 
        @include tablet {
            display: flex;
        }
        @include desktop {
            margin: 0 0 2.5rem 0;
        }
    }
    &__top {
        margin: 0 0 1.5rem 0; //bottom mobile 24px
        @include tablet {
            margin: 0 3.1vw 0 0; //24px right 
            min-width: 348px; //348px 
            max-width: 45.8vw; //420px 
        }
        @include desktop {
            margin: 0 1.9vw 0 0; //24px right
            min-width: 420px; //420px 
            max-width: 29.1vw; //420px in desktop xl 
        }
    }

    &__border {
        @include divider;
    }
}